body {
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  /* background-color: var(--b-primary); */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "minecraft";
  src: url(/Portfolio/static/media/Minecraft.d7b98c45.ttf);
}


:root{
  font-family: 'Open Sans';
  --t-primary:rgb(245, 237, 237);
  --t-secondary: #2272FF;
  --t-tertiary: #6ACFC7;
  --t-quad: #ed0a0a;
  --b-primary:rgb(0,0,0);
  --b-secondary:rgb(22, 24, 32);
  --b--tertiary: rgb(37, 43, 68);
  --b-s-transparent:rgba(22, 24, 32, 0.8);
}
.main-about {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 2rem;
    flex-wrap: wrap;
  }
  
  .title-about {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: var(--t-primary);
    font-size: 4rem;
    background-color: var(--b-s-transparent);
    outline: 0.1rem solid var(--t-tertiary);
    max-width: 40%;
    min-height: 30rem;
    border-radius: 3rem;
    padding: 2rem;
    margin-bottom: 2rem;
  }
  
  .about-img {
    max-width: 40%;
    max-height: 40rem;
    min-width: 30rem;
    border-radius: 3rem;
    padding: 2rem;
  }
  
  .description-about {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: var(--t-primary);
    max-width: 90%;
    font-size: 1.5rem;
  }
.main-container-projects{
    min-height: auto;
    min-width: auto;
    border-radius: 4rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    padding: 2rem;
    margin: 4rem;
}

.project-item{
    background-image: url(/Portfolio/static/media/sc-fi-texturing.8e502ecd.png);
    background-size: 100%;
    flex: 1 1;
    min-width: 30rem;
    max-width: 40rem;
    min-height: auto;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    outline: 0.1rem solid var(--t-secondary);
    border-radius: 1.875rem;
    margin: 2rem;
    padding: 2rem;
}

.project-photo{
    border-radius: 2rem;
    width: 90%;
    height: 25rem;
    margin: 0.5rem;
    outline: 0.2rem solid var(--t-tertiary);
    box-sizing: border-box;
}

.project-description{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: var(--t-primary);
    background-color: var(--b-s-transparent);
    min-width: 100%;
    margin: 0;
}

.project-title{
    padding-top: 0.8rem;
    font-size: 3rem;
    margin: 0.5rem;
}

.contains-description{
    font-size: 1.5rem;
    margin: 0;
    padding: 0.25rem;
}

.contains-tech{    
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: var(--t-primary);
    font-size: 1.5rem;
    background-color: var(--b-s-transparent);
    min-width: 100%;
    margin-top: 1rem;
    margin-left: 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}





  
img{
  -o-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.text{
  color: var(--t-primary);
  font-family: "Minecraft";
  font-size: 4rem;
  display: flex;
  flex-direction: center;
  align-items: center;
  max-width: 100%;
}

@media (max-width: 768px) {
  .home-page-container {
    height: 100vh;
    width: 100%;
    padding: 2rem;
  }
  
  #desk-image {
    width: 100%;
  }
  
  .text {
    font-size: 2rem;
  }
}
#tsparticles {
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
.nav-bar {
  background: var(--b-secondary);
  outline: 2px solid var(--t-secondary);
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
}

.profile-img{
  width: 5rem;
  height: 5rem;
  border-radius: 50px;
}

.photo-li{
  padding-left: 10px;
  margin: 0;
}

.nav-ul {
  font-family: 'Minecraft';
  font-size: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 1%;
  margin: 0;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.nav-li{
  position: relative;
  padding: 8px;
  margin-left: 30px;
  margin-right: 30px;
}

.nav-link {
  text-decoration: none;
  color: var(--t-primary);
}

.nav-link:visited {
  color: var(--t-primary);
}

.nav-link:hover{
  color: var(--t-secondary);
}

.active-link {
  position: relative;
  font-weight: bold;
  text-decoration: none;
  color: var(--t-secondary);
}

.active-link::after {
  content: '';
  position: absolute;
  bottom: -34px;
  left: 0;
  right: 0;
  height: 4px;
  background-color: var(--t-secondary);
  border-radius: 10px;
  box-shadow: 0 0 5px var(--t-secondary), 0 0 10px var(--t-secondary), 0 0 20px var(--t-secondary), 0 0 40px var(--t-secondary);
}

@media only screen and (max-width: 800px) {
  .nav-bar {
    width: 100%;
    padding: 5px;
    border-radius: 25px;
  }
  
  .nav-ul {
    font-size: 2rem;
  }
  
  .nav-li {
    padding: 5px;
    margin-left: 10px;
    margin-right: 10px;
  }
}

  
.Main-Container{
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--b-s-transparent);
    margin: 0 auto;
    padding: 4rem;
    z-index: 0;
    outline: 0.1rem solid var(--t-quad);
    max-width: 100rem;
}


@media only screen and (max-height: 600px) {
    .Main-Container {
      height: calc(100% - 4rem - 4rem - 2rem);
    }
  }
  
  @media only screen and (min-height: 601px) and (max-height: 900px) {
    .Main-Container {
      height: calc(100% - 4rem - 4rem - 4rem);
    }
  }
  
  @media only screen and (min-height: 901px) {
    .Main-Container {
      height: calc(100% - 4rem - 4rem - 6rem);
    }
  }
  

