.nav-bar {
  background: var(--b-secondary);
  outline: 2px solid var(--t-secondary);
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
}

.profile-img{
  width: 5rem;
  height: 5rem;
  border-radius: 50px;
}

.photo-li{
  padding-left: 10px;
  margin: 0;
}

.nav-ul {
  font-family: 'Minecraft';
  font-size: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  gap: 1rem;
  padding: 1%;
  margin: 0;
  backface-visibility: hidden;
}

.nav-li{
  position: relative;
  padding: 8px;
  margin-left: 30px;
  margin-right: 30px;
}

.nav-link {
  text-decoration: none;
  color: var(--t-primary);
}

.nav-link:visited {
  color: var(--t-primary);
}

.nav-link:hover{
  color: var(--t-secondary);
}

.active-link {
  position: relative;
  font-weight: bold;
  text-decoration: none;
  color: var(--t-secondary);
}

.active-link::after {
  content: '';
  position: absolute;
  bottom: -34px;
  left: 0;
  right: 0;
  height: 4px;
  background-color: var(--t-secondary);
  border-radius: 10px;
  box-shadow: 0 0 5px var(--t-secondary), 0 0 10px var(--t-secondary), 0 0 20px var(--t-secondary), 0 0 40px var(--t-secondary);
}

@media only screen and (max-width: 800px) {
  .nav-bar {
    width: 100%;
    padding: 5px;
    border-radius: 25px;
  }
  
  .nav-ul {
    font-size: 2rem;
  }
  
  .nav-li {
    padding: 5px;
    margin-left: 10px;
    margin-right: 10px;
  }
}

  